@import 'components/variables';

.calendario-vuelos {
  .table {
    tr {
      th, td {
        min-width: 100px;
        height: 90px;
        text-align: center;
        vertical-align: middle;
        border: 1px solid $gray-light;
      }
      .static {
        position: absolute;
        width: 100px;
      }
      .blank, .static-blank {
        border: 1px solid white;
        width: 100px;
        background-color: white;
      }
      .first-col {
        
      }
      .bg-title {
        padding: 0 0 0.5rem 0;
        background-color: $violet;
        opacity: 0.8;
        div {
          background-color: $orange;
          margin-bottom: 0.5rem;
          font-weight: 400;
          opacity: 0;
        }
        .day {
          font-size: 1.2rem;
          font-weight: 400;
        }
        .date {
          font-size: 1rem;
          font-weight: 600;
        }
      }
      th {
        color: white;
      }
      td {
        color: $violet;
        cursor: pointer;
      }
      .col1,
      .col2,
      .col3,
      .col4,
      .col5,
      .col6,
      .col7 {
        &:hover {
          color: white !important;
          background-color: $violet !important;
        }
      }
      .select {
        background-color: rgba($color: $violet, $alpha: 0.5);
        color: white;
      }
    }
  }
}